.order-form-button {
    display: block;
    margin: 52px auto 76px auto;
    padding: 16px 32px;
    width:max-content;
    background: #f5f5fa;
    box-shadow: -10px -10px 20px #ffffff, 10px 10px 20px rgba(72, 121, 150, 0.1),
      5px 5px 10px rgba(72, 121, 150, 0.25), -5px -5px 10px #ffffff;
    border-radius: 30.5px;
    cursor: pointer;
    border: none;
  
    font-family: var(--primary-font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 1.5;
  
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
  
    color: #487996;
    transition: box-shadow 300ms ease-out;
  
    &:hover {
      box-shadow: -12px -12px 22px #ffffff, 12px 12px 22px rgba(24, 125, 184, 0.1),
        7px 7px 22px rgba(35, 76, 99, 0.25), -7px -7px 12px #ffffff;
    }
  
    &:focus {
      box-shadow: -12px -12px 22px #ffffff, 12px 12px 22px rgba(23, 26, 27, 0.1),
        7px 7px 22px rgba(2, 25, 37, 0.25), -7px -7px 12px #ffffff;
    }          
  }
  
  .button__link {
    width: 100%;
    height: 100%;
    font-family: var(--primary-font-family);
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.1em;
    text-transform: uppercase;
    color: #487996;
  }
  @media screen and (min-width: 1440px) {
.order-form-button {    
    margin: 100px auto 100px auto;
}
  }